import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import { isMobile } from 'react-device-detect';
import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import obras from '../assets/ejes/obr.png';
import vacuna from '../assets/ejesProv/vacSF.png';
import incluir from '../assets/ejesProv/incluir.jpg';
import BegContent from './provContent/beg';
import VacunasContent from './provContent/vacunas';
import BilleteraContent from './provContent/billetera';
import Incluir from './provContent/incluir';
import Zoom from '@material-ui/core/Zoom';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired
};

/* function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
} */

export default function VerticalTabs() {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);
    const [btnProv, setBtnProv] = useState('beg');

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className={classes.root}>
            <Tabs
                orientation="vertical"
                variant="scrollable"
                value={value}
                onChange={handleChange}
                aria-label="Vertical tabs example"
                className={classes.tabs}
                indicatorColor={'primary'}
            >
                {provBtn.map((btn, i) => (
                    <ProvCard
                        i={i}
                        btn={btn}
                        btnProv={value}
                        setBtnProv={setValue}
                    />
                ))}
            </Tabs>
            <TabPanel value={value} index={0}>
                <Typography style={{ margin: 20, fontSize: 22 }}>
                    BOLETO EDUCATIVO GRATUITO
                </Typography>
                <BegContent />
            </TabPanel>
            <TabPanel value={value} index={1}>
                <Typography style={{ margin: 20, fontSize: 22 }}>
                    BILLETERA SANTA FE
                </Typography>
                <BilleteraContent />
            </TabPanel>
            <TabPanel value={value} index={2}>
                <Typography style={{ margin: 20, fontSize: 22 }}>
                    PROGRAMA FEDERAL INCLUIR SALUD
                </Typography>
                <Incluir />
            </TabPanel>
            <TabPanel value={value} index={3}>
                <Typography style={{ margin: 20, fontSize: 22 }}>
                    PLAN VACUNACION
                </Typography>
                <VacunasContent />
            </TabPanel>
        </div>
    );
}

const ProvCard = ({ btn, btnProv, setBtnProv, i }) => {
    return (
        <Zoom in={true} timeout={300 * i + 400}>
            <Card
                onClick={() => setBtnProv(i)}
                elevation={3}
                style={{
                    margin: 10,
                    backgroundColor: i === btnProv ? '#0c395c' : '#fff',
                    marginBottom: 6,
                    padding: 6,
                    borderRadius: '50%',
                    height: 100,
                    width: isMobile ? '25%' : 100,
                    alignSelf: 'center',
                    cursor: 'pointer'
                }}
            >
                <CardMedia
                    style={{ height: '100%', borderRadius: '50%' }}
                    image={btn.img}
                    title={btn.name}
                />
            </Card>
        </Zoom>
    );
};

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        //marginBottom: 10,
        backgroundColor: theme.palette.background.paper,
        display: 'flex',
        // height: 580,
        overflow: 'hidden',
        backgroundColor: 'transparent'
    },
    tabs: {
        borderRight: `1px solid ${theme.palette.divider}`,
        minWidth: 120
    }
}));

const provBtn = [
    { id: 'beg', img: begIcon, name: 'Boleto Educativo Gratuito' },
    { id: 'billetera', img: billetera, name: 'billetera Santa Fe' },
    { id: 'incluir', img: incluir, name: 'Plan Incluir' },
    { id: 'vacuna', img: vacuna, name: 'Plan de Vacunacion' }
];
