import React, { useState } from 'react';

import {
    Paper,
    Grid,
    Typography,
    makeStyles,
    CardMedia
} from '@material-ui/core';
import { billeteraData } from '../../info/provincia';
import ContentPaper from './contentPaper';
import { isMobile } from 'react-device-detect';

const BilleteraContent = () => {
    const classes = useStyles();

    return (
        <div style={{ textAlign: 'center' }}>
            <Grid container spacing={3}>
                <Grid item xs={12} sm={8}>
                    <Paper
                        style={{ height: 200, overflow: 'auto', padding: 10 }}
                    >
                        <Typography>{billeteraData.text}</Typography>
                    </Paper>
                    <Grid container spacing={isMobile ? 1 : 3}>
                        <Grid item xs={4} sm={4}>
                            <Paper className={classes.cardDesc}>
                                <Typography>
                                    {billeteraData.card4.txtA}
                                </Typography>
                                <Typography>
                                    {billeteraData.card4.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item xs={4} sm={4}>
                            <Paper className={classes.cardDesc}>
                                <img
                                    src={billeteraData.card2.txtC}
                                    style={{
                                        height: 25,
                                        width: 25,
                                        marginTop: 5
                                    }}
                                />
                                <Typography>
                                    {billeteraData.card2.txtA}
                                </Typography>
                                <Typography>
                                    {billeteraData.card2.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item xs={4} sm={4}>
                            <Paper className={classes.cardDesc}>
                                <img
                                    src={billeteraData.card3.txtC}
                                    style={{
                                        height: 25,
                                        width: 25,
                                        marginTop: 5
                                    }}
                                />
                                <Typography>
                                    {billeteraData.card3.txtA}
                                </Typography>
                                <Typography>
                                    {billeteraData.card3.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} sm={4}>
                    <Paper
                        style={{
                            height: 90,
                            marginBottom: 20,
                            overflow: 'auto'
                        }}
                    >
                        <Typography>{billeteraData.card0.txtA}</Typography>
                        <Typography>{billeteraData.card0.txtB}</Typography>
                    </Paper>
                    <Paper
                        style={{
                            height: 90,
                            marginBottom: 20,
                            overflow: 'auto'
                        }}
                    >
                        <Typography>{billeteraData.card1.txtA}</Typography>
                        <Typography>{billeteraData.card1.txtB}</Typography>
                    </Paper>
                    <Paper
                        style={{
                            height: 90,
                            overflow: 'auto'
                        }}
                    >
                        <Typography>{billeteraData.card5.txtA}</Typography>
                        <Typography>{billeteraData.card5.txtB}</Typography>
                    </Paper>
                </Grid>
            </Grid>
        </div>
    );
};

const useStyles = makeStyles(() => ({
    cardDesc: {
        marginTop: 20,
        height: 100
    }
}));

export default BilleteraContent;
