import React, { useState } from 'react';
import { Paper, Grid, Typography, CardMedia, Card } from '@material-ui/core';
import ContentPaper from './contentPaper';
import playLogo from '../../assets/ejesProv/beg/playlogo.png';
import pregFrec from '../../assets/ejesProv/beg/pregFrec.png';
import num0800 from '../../assets/ejesProv/beg/num0800.png';
import PaperCard from '../../custom/paperCard';
import { makeStyles } from '@material-ui/core/styles';
import colores from '../../styles/colors';
import { begData } from '../../info/provincia';
import { isMobile } from 'react-device-detect';

const BegContent = () => {
    const classes = useStyles();

    return (
        <div>
            <Grid container spacing={3}>
                <Grid item xs={12} sm={8}>
                    <PaperCardText>{begData.text}</PaperCardText>
                    <Card>
                        <Typography
                            style={{ fontSize: 18, margin: 5, marginLeft: 15 }}
                        >
                            {begData.card1.txtA}
                        </Typography>
                        <Grid container spacing={isMobile ? 1 : 3}>
                            <Grid item xs={6} sm={6}>
                                <PaperCardText>
                                    {begData.card1.txtB}
                                </PaperCardText>
                            </Grid>
                            <Grid item xs={6} sm={6}>
                                <PaperCardText>
                                    {begData.card1.txtC}
                                </PaperCardText>
                            </Grid>
                        </Grid>
                    </Card>
                </Grid>

                <Grid item xs={12} sm={4}>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={12}>
                            <CardMedia
                                image={begData.card2}
                                style={{
                                    margin: 5,
                                    height: isMobile ? 60 : 75
                                }}
                            />
                        </Grid>
                        <Grid item xs={12} sm={12}>
                            <CardMedia
                                image={begData.card4.img}
                                style={{
                                    height: isMobile ? 80 : 90
                                }}
                            />
                        </Grid>
                        <Grid item xs={12} sm={12}>
                            <CardMedia
                                image={begData.card5.img}
                                style={{
                                    height: isMobile ? 80 : 90
                                }}
                            />
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>

            <Grid container spacing={3}>
                <Grid item xs={12} sm={4}>
                    <PaperCardMedia media={begData.card3.img} />
                </Grid>
                <Grid item xs={12} sm={8}>
                    <Grid container spacing={3}>
                        <Grid item sm={12}>
                            <Card>
                                <Typography
                                    style={{ fontSize: 22, margin: 10 }}
                                >
                                    {begData.card6.txtA}
                                </Typography>
                                <PaperCardText>
                                    {begData.card6.txtB}
                                </PaperCardText>
                            </Card>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </div>
    );
};

const PaperCardText = (props) => {
    return (
        <Paper
            style={{
                padding: isMobile ? 10 : 20,
                backgroundColor: colores.gamAalt1,
                margin: 5,
                marginBottom: 20,
                height: 130,
                marginBottom: isMobile ? 0 : 20,
                overflow: 'auto'
            }}
        >
            <Typography
                style={{
                    fontSize: isMobile ? 13 : 15 /*  textAlign: 'center' */
                }}
            >
                {props.children}
            </Typography>
        </Paper>
    );
};

const PaperCardMedia = ({ media }) => {
    return (
        <Card
            style={{
                padding: 0,
                backgroundColor: 'transparent',
                margin: 10,
                height: 120,
                overflow: 'hidden',
                borderRadius: 25
            }}
        >
            <CardMedia image={media} style={{ height: '100%' }} />
        </Card>
    );
};

const useStyles = makeStyles(() => ({}));

export default BegContent;
