import {
    Paper,
    Typography,
    Grow,
    Card,
    CardMedia,
    CardContent
} from '@material-ui/core';
import React from 'react';
import colores from '../styles/colors';
import { isMobile } from 'react-device-detect';
import jorAmbiente from '../assets/jor/jorAmbiente.jpg';

const EjesContent = ({ data, children }) => {
    return (
        <div>
            <Card style={{ height: 110, marginBottom: 10 }}>
                <CardMedia image={data.img} style={{ height: '100%' }} />
            </Card>
            <Paper
                style={{
                    height: 240,
                    overflow: 'auto',
                    padding: isMobile ? 10 : 20,
                    margin: 'auto',
                    width: isMobile ? '100%' : '80%',
                    textAlign: isMobile ? 'center' : 'justify',
                    backgroundColor: colores.cardGrIco2
                }}
            >
                <Typography
                    style={{
                        fontSize: isMobile ? 18 : 24,
                        letterSpacing: isMobile ? 1 : 1.4
                    }}
                >
                    {data.text}
                </Typography>
            </Paper>
            <Card style={{ margin: 20 }}>
                <CardContent>
                    <Typography>{data.card1.txtA}</Typography>
                </CardContent>
            </Card>
        </div>
    );
};

export default EjesContent;
