import React, { forwardRef, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Grid, IconButton, makeStyles, Avatar } from '@material-ui/core';
import { FacebookProvider, EmbeddedPost } from 'react-facebook';
import AmbienteContent from './ejesContent/ambiente';
import DeportesContent from './ejesContent/deportes';
import GeneroContent from './ejesContent/genero';
import BarriosContent from './ejesContent/barrios';
import EjesContent from '../custom/ejesContent';
import Grow from '@material-ui/core/Grow';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import AppBar from '@material-ui/core/AppBar';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardMedia from '@material-ui/core/CardMedia';
import ecoAm from '../assets/jor/jorAmbiente.jpg';
import colores from '../styles/colors';
import EcoIcon from '@material-ui/icons/Eco';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import fem from '../assets/iconos/fem.png';
import genero from '../assets/jor/jorGenero.jpg';
import deportes from '../assets/jor/jorDeportes.jpg';
import barriosJor from '../assets/jor/jorBarrios.jpg';
import DomainIcon from '@material-ui/icons/Domain';

const EjesModal = ({ open, handleClose }) => {
    const [swCont, setSwCont] = useState('ambiente');
    const classes = useStyles();
    const [dataEje, setDataEje] = useState(ejesC[0]);

    function switchContent(cont) {
        switch (cont) {
            case 'ambiente':
                return <AmbienteContent />;
                break;

            case 'deporte':
                return <DeportesContent />;
                break;

            case 'genero':
                return <GeneroContent />;
                break;

            case 'barrios':
                return <BarriosContent />;
                break;

            default:
                return <></>;
                break;
        }
    }

    return (
        <div>
            <Dialog
                fullScreen
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar className={classes.appBar}>
                    <Toolbar style={{ backgroundColor: colores.mainLightfull }}>
                        <IconButton
                            edge="start"
                            color={colores.mainDarkfull}
                            onClick={handleClose}
                            aria-label="close"
                        >
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            Ejes De Trabajo
                        </Typography>
                    </Toolbar>
                </AppBar>
                <div
                    style={{
                        height: '100%',
                        paddingLeft: isMobile ? 12 : 90,
                        paddingRight: isMobile ? 12 : 90,
                        paddingTop: 20,
                        backgroundColor: colores.fondoLight,
                        textAlign: 'center'
                    }}
                >
                    <ContentEjes
                        setSwCont={setSwCont}
                        swCont={swCont}
                        setDataEje={setDataEje}
                    />
                    <Divider style={{ marginTop: 15, marginBottom: 15 }} />
                    <EjesContent data={dataEje} />
                    {/* 	<EjesContent>
						{switchContent(swCont)}
					</EjesContent> */}

                    {/* {!isMobile &&  <ContentFacebook /> } */}
                </div>
            </Dialog>
        </div>
    );
};

const ContentFacebook = () => {
    const FbPost = [
        'https://www.facebook.com/jorgelinamudallel/posts/4102542703165503',
        'https://www.facebook.com/jorgelinamudallel/posts/4157726427647130',
        'https://www.facebook.com/jorgelinamudallel/posts/4195750497178056'
        //	'https://www.facebook.com/jorgelinamudallel/posts/4177032202383219'
    ];

    return (
        <FacebookProvider appId="865953314130137">
            <Grid container spacion={3} style={{ alignContent: 'center' }}>
                {FbPost.map((post) => (
                    <Grid item sm={4}>
                        <EmbeddedPost
                            width={isMobile ? 200 : 250}
                            href={post}
                        />
                    </Grid>
                ))}
            </Grid>
        </FacebookProvider>
    );
};

const ContentEjes = ({ setSwCont, swCont, setDataEje }) => {
    return (
        <Grid
            container
            spacing={isMobile ? 1 : 4}
            style={{
                width: isMobile ? '100%' : '85%',
                marginRight: 'auto',
                marginLeft: 'auto'
            }}
        >
            {ejesC.map((eje, i) => (
                <Grow in={true} mountOnEnter timeout={400 * i + 400}>
                    <Grid item xs={3} sm={3}>
                        <CardEjes
                            eje={eje}
                            setSwCont={setSwCont}
                            swCont={swCont}
                            setDataEje={setDataEje}
                        />
                    </Grid>
                </Grow>
            ))}
        </Grid>
    );
};

const CardEjes = ({ eje, setSwCont, swCont, setDataEje }) => {
    return (
        <Card
            elevation={swCont === eje.id ? 1 : 5}
            style={{
                //width: isMobile && 80,
                padding: isMobile ? 2 : 0,
                marginLeft: eje.id === 'ambiente' && -4,
                borderRadius: isMobile ? '15%' : 15,
                textAlign: 'center',
                color: swCont !== eje.id && eje.color,
                backgroundColor:
                    swCont === eje.id ? eje.color : colores.mainLight
            }}
            onClick={() => {
                setSwCont(eje.id);
                setDataEje(eje);
            }}
        >
            <CardActionArea>
                {isMobile ? (
                    eje.ico
                ) : (
                    <CardMedia
                        style={{
                            height: 100,
                            marginTop: 10,
                            marginRight: 10,
                            marginLeft: 10,
                            borderRadius: 15
                        }}
                        image={eje.img}
                        title={eje.title}
                    />
                )}
                <Typography
                    gutterBottom
                    variant="h5"
                    style={{
                        fontSize: isMobile ? 12 : 18,
                        marginTop: 5,
                        color: eje.letra
                    }}
                >
                    {eje.name}
                </Typography>
            </CardActionArea>
        </Card>
    );
};

const ejesC = [
    {
        id: 'ambiente',
        name: isMobile ? 'Ambiente' : 'Medio Ambiente',
        ico: <EcoIcon />,
        img: ecoAm,
        letra: colores.cardGrTex2,
        color: colores.cardGradiant2,
        borde: colores.cardGrTex2,
        text: `Trabajo a través de diferentes proyectos y campañas en la construcción de una conciencia colectiva en materia de reciclaje, 
		reutilización y cuidado de nuestro ambiente, como eje fundamental para el desarrollo sustentable de nuestra ciudad.`,
        card1: {
            txtA: 'Huerta Familiar'
        },
        card2: {
            txtA: 'eco canje'
        }
    },
    {
        id: 'deporte',
        name: 'deportes',
        ico: <SportsSoccerIcon />,
        img: deportes,
        letra: colores.cardGrTex3,
        color: colores.cardGradiant3,
        borde: colores.cardGrTex3,
        text: `El deporte es una de las herramientas fundamentales hacia la inclusión, formación y contención de niños,
		 niñas y adolescentes. El rol que ocupan debe ser reivindicado y defendido desde acciones concretas que los auxilien.`,
        card1: {
            txtA: ''
        },
        card2: {
            txtA: ''
        }
    },
    {
        id: 'genero',
        name: 'Género',
        ico: (
            <Avatar
                src={fem}
                style={{ height: 24, width: 24, margin: 'auto', marginTop: 2 }}
            />
        ),
        img: genero,
        letra: colores.cardGrTex1,
        color: colores.cardGradiant1,
        borde: colores.cardGrTex1,
        text: `Desde mi lugar, formo parte de la lucha hacia la paridad, inclusión, empoderamiento e igualdad de 
		oportunidades para mujeres e identidades disidentes de nuestra ciudad.`,
        card1: {
            txtA: `Contra violencia de genero`,
            txtB: '*144',
            txtC: 'lunes a viernes de 08:00 a 20:00hs'
        },
        card2: {
            txtA: ''
        }
    },
    {
        id: 'barrios',
        name: 'Barrios',
        ico: <DomainIcon />,
        img: barriosJor,
        letra: colores.cardGrTex4,
        color: colores.cardGradiant4,
        borde: colores.cardGrTex4,
        text: `Desde mi lugar BARRIOS, formo parte de la lucha hacia la paridad, inclusión, empoderamiento e igualdad de 
		oportunidades para mujeres e identidades disidentes de nuestra ciudad.`,
        card1: {
            txtA: ''
        },
        card2: {
            txtA: ''
        }
    }
];

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
    appBar: {
        position: 'sticky'
    },

    title: {
        marginLeft: 5,
        color: colores.mainDarkfull
    }
}));

export default EjesModal;
