import React, { useState } from 'react';

import { Paper, Grid, Typography, makeStyles } from '@material-ui/core';
import { useEffect } from 'react';
import { vacunasData } from '../../info/provincia';

const VacunasContent = () => {
    const classes = useStyles();

    return (
        <div>
            <Grid container spacing={3}>
                <Grid item sm={8}>
                    <Grid container spacing={3}>
                        <Grid item sm={12}>
                            <Paper
                                style={{
                                    height: 200,
                                    overflow: 'auto',
                                    padding: 10
                                }}
                            >
                                <Typography>{vacunasData.text}</Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>

                <Grid item sm={4}>
                    <Grid container spacing={1}>
                        <Grid item xs={12} sm={12}>
                            <Paper
                                style={{
                                    height: 100,
                                    overflow: 'auto',
                                    padding: 10
                                }}
                            >
                                <Typography>
                                    {vacunasData.card0.txtA}
                                </Typography>
                                <Typography>
                                    {vacunasData.card0.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item sm={12}>
                            <Paper
                                style={{
                                    height: 100,
                                    overflow: 'auto',
                                    padding: 10
                                }}
                            >
                                <Typography>
                                    {vacunasData.card4.txtA}
                                </Typography>
                                <Typography>
                                    {vacunasData.card4.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item sm={4}>
                    <Paper
                        style={{
                            height: 200,
                            overflow: 'auto',
                            padding: 10
                        }}
                    >
                        <Typography>{vacunasData.card1.txtA}</Typography>
                        <Typography>{vacunasData.card1.txtB}</Typography>
                        <Typography>{vacunasData.card1.txtC}</Typography>
                    </Paper>
                </Grid>
                <Grid item sm={4}>
                    <Paper
                        style={{
                            height: 200,
                            overflow: 'auto',
                            padding: 10
                        }}
                    >
                        <Typography>{vacunasData.card2.txtA}</Typography>
                        <Typography>{vacunasData.card2.txtB}</Typography>
                    </Paper>
                </Grid>
                <Grid item sm={4}>
                    <Paper
                        style={{
                            height: 200,
                            overflow: 'auto',
                            padding: 10
                        }}
                    >
                        <Typography>{vacunasData.card5.txtA}</Typography>
                        <Typography>{vacunasData.card5.txtB}</Typography>
                    </Paper>
                </Grid>
            </Grid>
        </div>
    );
};

const useStyles = makeStyles(() => ({
    cardDesc: {
        marginTop: 20,
        height: 100
    }
}));

export default VacunasContent;
