import pregFrec from '../assets/ejesProv/beg/pregFrec.png';
import solicitar from '../assets/ejesProv/beg/solicitar.png';
import playLogo from '../assets/ejesProv/beg/playlogo.png';
import begFoto from '../assets/ejesProv/beg/begFoto.png';
import contactoBilletera from '../assets/ejesProv/billetera/apple.png';
import desc20 from '../assets/ejesProv/billetera/20.png';
import desc30 from '../assets/ejesProv/billetera/30.png';
import Beg from './provContent/beg';
import Billetera from './provContent/billetera';
import Vacunas from './provContent/vacunas';
import Incluir from './provContent/incluir';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`scrollable-force-tabpanel-${index}`}
            aria-labelledby={`scrollable-force-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={0}>
                    <Typography style={{ textAlign: 'center' }}>
                        {children}
                    </Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired
};

const ProvModal = ({ open, handleClose }) => {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    return (
        <div>
            <Dialog
                fullScreen
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar className={classes.appBar}>
                    <Toolbar
                        style={{
                            backgroundColor: colores.mainLightfull,
                            flexDirection: 'row'
                        }}
                    >
                        <IconButton
                            edge="start"
                            color={colores.mainDarkfull}
                            onClick={handleClose}
                            aria-label="close"
                        >
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            Accion Con Provincia:
                        </Typography>
                    </Toolbar>

                    {isMobile && (
                        <TabsProv
                            provBtn={provBtn}
                            value={value}
                            setValue={setValue}
                        />
                    )}
                </AppBar>
                <div
                    style={{
                        height: '100%',
                        paddingLeft: isMobile ? 10 : 100,
                        paddingRight: isMobile ? 10 : 100,
                        paddingTop: 20,
                        backgroundColor: colores.fondoLight
                    }}
                >
                    {!isMobile && <VerticalTabs />}
                    {isMobile &&
                        provBtn.map((tab, i) => (
                            <TabPanel value={value} index={i}>
                                <ContentProvMobile tab={tab}>
                                    {tab.tabContent}
                                </ContentProvMobile>
                            </TabPanel>
                        ))}
                </div>
            </Dialog>
        </div>
    );
};

const ContentProvMobile = ({ tab, children }) => {
    return (
        <>
            <Typography
                style={{
                    fontSize: 22,
                    marginBottom: 10
                }}
            >
                {tab.name}
            </Typography>
            {/*  <Card style={{ padding: 10 }}>
                <Typography
                    style={{
                        fontSize: 16,
                        maxHeight: 220,
                        overflow: 'auto'
                    }}
                >
                    {tab.text}
                </Typography>
            </Card> */}
            {children}
        </>
    );
};

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

